.main {
  margin-top: 30px;
}

.countries-container {
  max-width: 1280px;
  min-height: 75vh;
  flex-wrap: wrap;
  margin: auto;
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  padding-bottom: 50px;
  a {
    min-width: 300px;
    width: 370px;
    text-decoration: none;
  }

  &--onload {
    align-items: center;
  }
}

.country-card {
  width: 250px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 10px;
}

.country-card__photo {
  width: 90%;
}

.hidden {
  display: none !important;
}
